@mixin addorremove {
    font-size: 1.4vw;
    background:	#A8A8A8;
    cursor: pointer;
    &:active{
        background: gray;
    }
  }
@mixin buy {
  position: relative;
  top: 6vh;
  left: 18.8vw;
  font-family: Arial;
  margin-top: 20vh;
  -webkit-user-drag: none;
  
  b{
      margin-left: 12vw;
      
  }
  #number{
    margin-left: 10vw;
    font-weight: normal;
    position: relative;
    left: 2vw;
    
  }
  div{
      padding-top: 2vh;
      
  }
  button{
      margin-left: 0vw;
      position: relative;
      left: 7vw;
      border: none;
      background: white;
      font-size: large;
      cursor: pointer;
      
      
    }
  #compra{
      position: relative;
      left: -3vw;
      
  }
  img{
      height: 20vh;
      width: 15vw;
      position: relative;
      left: -4vw;
      top: 7vh;
      -webkit-user-select: none;  
      -moz-user-select: none;    
      -ms-user-select: none;      
      user-select: none;
     -webkit-user-drag: none;
     
    }
  #price{
      position: relative;
      left: 3vw;
      font-weight: normal;
      
       }

       
  #increment{
    @include addorremove;
    
  }

  #decrease{
    @include addorremove;
    width:1.6vw;
    
    
  }
}

